import { CST } from "../CST";

export class GameScene extends Phaser.Scene{
    constructor(){
        super({
            key: CST.SCENES.GAME
        })
    }
init(){
}
create(){
    var platforms, cursors; //Variable Initialization
    

    this.add.image(400, 300, "stage"); //Stage Background

    //Platforms
    platforms = this.physics.add.staticGroup(); //Creating Platform Object Static Group

    platforms.create(50, 460, "ground").setScale(.2).refreshBody();
    platforms.create(250, 460, "landing").setScale(.3).refreshBody();
    platforms.create(380, 410, "ramp").setScale(.3).refreshBody();
    platforms.create(550, 390, "ground").setScale(.2).refreshBody();

    //Player
    var player = this.physics.add.sprite(50, 425, "brandon");

    player.setBounce(0.2);
    player.setCollideWorldBounds(true);

    this.anims.create({
        key: 'left',
        frames: this.anims.generateFrameNumbers("brandon", {start:0, end:3}),
        frameRate: 10,
        repeat: -1
    });
    this.anims.create({
        key: 'right',
        frames: this.anims.generateFrameNumbers("brandon", {start:0, end:-3}),
        frameRate: 10,
        repeat: -1
    });

    this.physics.add.collider(player, platforms); //Collision Checker
    
    }
update(){
}
}